import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			// openid
			openid: null,
			// 手机号码
			phoneNumber: null,
			// 线路特色富文本的内容
			textVal: '<h1>这是富文本内容</h1>',
			// 传递过来的 页面详情的 id
			ProductId: '',
			// 图文详情
			details: '',
			// 行程介绍
			tripIntroduce: ''
		}
	},
	methods: {
		// 判断是否登录了 登录了下单 未去登录页面
		getLogin() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					var code = res.code
					this.$http.post('/api/WeiXinApplet.ashx', {
						action: "Login",
						code: code
					}).then(res => {
						console.log(res)
						if (res.data.status == 'true') {
							uni.setStorageSync('openid', res.data.openid)
							uni.setStorageSync('session_key', res.data.session_key)
							uni.setStorageSync('UserId', res.data.UserId)
							if (res.data.UserId) {
								this.getUser()
							}
						} else {
							uni.showToast({
								title: '获取信息异常，请稍后重新操作！',
								duration: 2000,
								icon: 'none'
							});
						}
					})
				}
			});
		},
		// 获取用户信息
		getUser() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (res) => {
					console.log(res)
					if (res.errMsg == 'getUserInfo:ok') {
						uni.setStorageSync('avatarUrl', res.userInfo.avatarUrl)
						uni.setStorageSync('nickName', res.userInfo.nickName)
						this.$http.post('/api/WeiXinApplet.ashx', {
							action: 'UserBindInfo',
							UserId: uni.getStorageSync('UserId'),
							nickName: res.userInfo.nickName,
							gender: res.userInfo.gender,
							avatarUrl: res.userInfo.avatarUrl
						}).then(res => {
							console.log(res)
							if (res.data.status == 'true') {
								uni.navigateTo({
									url: '/pages/login/login?ProductId=' + this.ProductId
								});
							}
						})
					}
				}
			});
		},
		// 打开弹出框
		open() {
			// 如果手机号码为空 打开弹框
			if (this.phoneNumber == '') {
				this.$refs.popup.open()
			} else {
				// 去选择出行时间 / 人数页面
				uni.navigateTo({
					url: '/pages/choice/choice?productId=' + this.ProductId
				});
			}
		},
		// 去登录页面
		goLogin() {
			uni.navigateTo({
				url: '/pages/login/login?ProductId=' + this.ProductId
			});
		},
		// 获取详情内容
		GetProductByID() {
			this.$http.post('/api/WeiXinApplet.ashx', {
				action: 'GetProductByID',
				productId: this.ProductId
			}).then(res => {
				if (res.data.status == true) {
					this.details = res.data.Data
				}
			})
		},
		// 获取行程介绍的内容
		GetProductTripByProductID() {
			this.$http.post('/api/WeiXinApplet.ashx', {
				action: 'GetProductTripByProductID',
				productId: this.ProductId
			}).then(res => {
				console.log(res)
				if (res.data.status == true) {
					this.tripIntroduce = res.data.Data.Data
					console.log(this.tripIntroduce)
				}
			})
		},
		// 拨打号码
		goMakePhoneCall() {
			uni.makePhoneCall({
				phoneNumber: '17855355076' //仅为示例
			});
		}
	},
	computed: {
		isOpenid() {
			this.phoneNumber = uni.getStorageSync('phoneNumber')
			this.openid = uni.getStorageSync('openid')
			if (this.openid == '') {
				return true
			}
			return false
		}
	},
	onLoad(options) {
		this.ProductId = options.ProductId
		if (this.ProductId != undefined) {
			// 获取详情内容
			this.GetProductByID()
			// 获取行程介绍的内容
			this.GetProductTripByProductID()
		}
	}
}
